import {securityAPI, authAPI} from '../api/api'
import {stopSubmit} from 'redux-form'

const SET_USER_DATA   = 'auth/SET_USER_DATA'
const TOGGLE_FETCHING = 'auth/TOGGLE_FETCHING'
const GET_CAPTCHA_URL = 'auth/GET_CAPTCHA_URL'

let initialState = {
	userId: null,
	email: null,
	login: null,
	isAuth: false,
	isFetching: true,
	captchaUrl: null
}

const authReducer = (state = initialState, action) => {
	switch(action.type) {
		case SET_USER_DATA:
		case GET_CAPTCHA_URL:
			return {...state, ...action.payload}

		case TOGGLE_FETCHING:
			return {...state, isFetching: action.isFetching}

		default:
			return state
	}
}

// ACTION CREATORS
export const setAuthUserData = (userId, email, login, isAuth) =>
	({ type: SET_USER_DATA, payload: {userId, email, login, isAuth} })
export const setFetching = (isFetching) => ({ type: TOGGLE_FETCHING, isFetching })
export const getCaptchaUrlSuccess = (captchaUrl) => ({ type: GET_CAPTCHA_URL,  payload: {captchaUrl} })

// THUNK CREATORS
export const getAuthUserData = () => async (dispatch) => {
	dispatch(setFetching(true))
	const response = await authAPI.me()
	dispatch(setFetching(false))
	if (response.resultCode === 0) {
		let {id, email, login} = response.data
		dispatch(setAuthUserData(id, email, login, true))
	}
}

export const login = (email, password, remember, captcha) => async (dispatch) => {
	dispatch(setFetching(true))
	const response = await authAPI.login(email, password, remember, captcha)
	dispatch(setFetching(false))
	if (response.data.resultCode === 0) {
		dispatch(getAuthUserData())
	} else {
		if (response.data.resultCode === 10) {
			dispatch(getCaptchaURL())
		}

		let message = response.data.messages.length > 0 ? response.data.messages[0] : 'Unknown error'
		dispatch(stopSubmit('login', {_error: message}))
	}
}

export const getCaptchaURL = () => async (dispatch) => {
	dispatch(setFetching(true))
	const response = await securityAPI.getCaptchaUrl()
	dispatch(setFetching(false))
	const captchaUrl = response.data.url
	dispatch(getCaptchaUrlSuccess(captchaUrl))
}

export const logout = () => async (dispatch) => {
	dispatch(setFetching(true))
	const response = await authAPI.logout()
	dispatch(setFetching(false))
	if (response.data.resultCode === 0) {
		dispatch(setAuthUserData(null, null, null, false))
	}
}

export default authReducer
